  
//! 页底推广词

.mt-6 {margin-top: 4rem;}

.ad-above-footer p {
  color: lighten($text-color-body, 40%);
  line-height: $line-height-high;
}


//! 网页页脚

// 页脚文本格式

.docsimpo-text-lightblue-1 {color: lighten($background-color-head, 15%);}

.docsimpo-text-lightblue-2 {color: lighten($background-color-head, 30%);}

#footer {
  h6 {@extend .docsimpo-text-lightblue-2}
  p {@extend .docsimpo-text-lightblue-1}
}
 

//! 微信二维码

.media {position: relative;}

#icon-linkedin, #icon-wechat {opacity: 0.7;}

#icon-linkedin:hover, #icon-wechat:hover {opacity: 1;}

#icon-linkedin:hover {border-bottom: 2px solid #fff;}

// 二维码外观
.qr{
  $bgcolor: $background-color-head;
  display: inline-block; /*! 删掉这一行，二维的码的边框就聚拢到中心了。可考虑设置在img上 */
  padding: 10px 10px; /* 二维码的边框宽度 */
  background-image: radial-gradient(lighten($bgcolor, 10%), darken($bgcolor, 10%));
  border-radius: 5px;
}

// 将二维码定位在页脚上方，并隐藏
#wechat-qr {
  position: absolute;
  top: -6rem;
  left: 9rem;
  display: none;
}


//! 点击、鼠标滑过时的动作

#wechat-qr-checkbox {
  display: none;

  @media screen and (max-width: 767px) {
    &:checked ~ #wechat-qr {
      display: inline-block;
    }
  }

}

@media screen and (min-width: 768px) {
  #icon-wechat:hover ~ #wechat-qr {display: inline-block;}
}

